import React, { ChangeEvent } from 'react';
import { FormControl, FormControlProps } from 'react-bootstrap'
import { useField, useFormikContext, FieldConfig } from 'formik';

interface SelectValues {
  name: string;
  noError?: boolean;
}

const TextfieldWrapper = ({ name, noError }: SelectValues & FieldConfig<FormControlProps>) => {
  const { setFieldValue } = useFormikContext();
  const [field, meta] = useField(name);

  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { value } = event.target;
    setFieldValue(name, value);
  };

  const configSelect: FormControlProps = {};

  if (meta && meta.touched && meta.error) {
    configSelect.isInvalid = true;
  }

  const isError = () => {
    return !!configSelect.isInvalid && !!meta.error && !!!noError
  }
  
  return (
    <div className='textfield-wrapper'>
      <FormControl
        onChange={(event: ChangeEvent<HTMLInputElement>) => handleChange(event)}
        className={`textfield-wrapper__input ${ isError() ? 'error-input' : ''}`}
      />
      { isError() ? <span className='error-text'>{meta.error}</span> : null }
    </div>
  );
};

export default TextfieldWrapper;