import React, { ChangeEvent } from 'react';
import { FormCheck, FormCheckProps } from 'react-bootstrap'
import { useField, useFormikContext, FieldConfig } from 'formik';
import './CheckboxWrapper.scss';

interface SelectValues {
  name: string;
}

const CheckboxWrapper = ({ name }: SelectValues & FieldConfig<FormCheckProps>) => {
  const { setFieldValue } = useFormikContext();
  const [field, meta] = useField(name);

  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { checked } = event.target;
    setFieldValue(name, checked);
  };

  const configSelect: FormCheckProps = {};

  if (meta && meta.touched && meta.error) {
    configSelect.isInvalid = true;
  }

  const isError = () => {
    return !!configSelect.isInvalid && !!meta.error;
  }

  return (
    <div className='checkbox-wrapper'>
      <label className='checkbox-wrapper__label'>
        <FormCheck
          onChange={(event: ChangeEvent<HTMLInputElement>) => handleChange(event)}
          className={`${isError() ? 'error-input' : ''} checkbox-wrapper__input`}
        />
        <p className={`checkbox-wrapper__description ${isError() ? 'error-text' : ''}`}>I agree to <a href="/" className='link-primary'>Bolt's Terms of Service</a> and <a href="/" className='link-primary'>Privacy Policy</a></p>
      </label>
    </div>
  );
};

export default CheckboxWrapper;