import React, { ChangeEvent } from 'react';
import { FormSelect, FormSelectProps } from 'react-bootstrap'
import { useField, useFormikContext, FieldConfig } from 'formik';

interface SelectValues {
  name: string;
  options: Record<string, string>;
}

const SelectWrapper = ({ name, options }: SelectValues & FieldConfig<FormSelectProps>) => {
  const { setFieldValue } = useFormikContext();
  const [field, meta] = useField(name);

  const handleChange = (event: ChangeEvent<HTMLSelectElement>) => {
    const { value } = event.target;
    setFieldValue(name, value);
  };

  const configSelect: FormSelectProps = {
  };

  if (meta && meta.touched && meta.error) {
    configSelect.isInvalid = true;
  }

  const isError = () => {
    return !!configSelect.isInvalid && !!meta.error;
  }

  return (
    <div className='select-wrapper'>
      <FormSelect
        onChange={(event: ChangeEvent<HTMLSelectElement>) => handleChange(event)}
        className={`select-wrapper__input ${ isError() ? 'error-input' : ''}`}
      >
        {Object.keys(options).map((item, pos) =>
          <option key={pos} value={item}>{options[item]}</option>
        )}
      </FormSelect>
      { isError() ? <span className='error-text'>{meta.error}</span> : null }
    </div>
  );
};

export default SelectWrapper;
