import React from 'react';
import { Button, Col, Row, Stack } from 'react-bootstrap'
import { Formik, Form } from 'formik';
import * as Yup from 'yup';
import './FormWrapper.scss';
import CheckboxWrapper from '../CheckboxWrapper';
import SelectWrapper from '../SelectWrapper';
import TextfieldWrapper from '../TextfieldWrapper';

const FormWrapper = () => {
  const cities = {
    "0": "Tallinn",
    "1": "Tartu",
  };

  const codes = {
    "EST": "+372",
    "LT": "+371",
  };
  
  const INITIAL_FORM_STATE = {
    email: '',
    phonePrefix: codes['EST'],
    phoneNumber: '',
    city: cities[0],
    termsOfService: false
  };

  const FORM_VALIDATION = Yup.object().shape({
    email: Yup.string()
      .required('Email is required')
      .email('Invalid email.'),
    phonePrefix: Yup.string()
      .when('phoneNumber', {
        is: '',
        then: Yup.string().required('Phone is required'),
      }),
    phoneNumber: Yup.number()
      .required('Phone is required')
      .typeError('Please enter a valid phone number')
      .integer()
      .when('phonePrefix', {
        is: '',
        then: Yup.string().required('Phone is required')
    }),
    city: Yup.string()
      .required('City is required'),
    termsOfService: Yup.boolean()
      .oneOf([true], 'The terms and conditions must be accepted.')
      .required('The terms and conditions must be accepted.'),
  }, [['phoneNumber', 'phonePrefix']]);

  return (
    <div className='form-wrapper'>
      <Formik
        initialValues={INITIAL_FORM_STATE}
        validationSchema={FORM_VALIDATION}
        onSubmit={(data) => { console.log(data); }}
      >
        <Form>
          <h3 className='form-wrapper__title'>Become a bolt driver</h3>
          <h4 className='form-wrapper__description'>If you have multiple cars or drivers <a href="/" className='link-primary'>sign up as a fleet owner.</a></h4>
          <Stack gap={3} direction="vertical">
            <Col>
              <p className='form-wrapper__input-name'>Email</p>
              <TextfieldWrapper name="email" />
            </Col>
            <Col>
              <p className='form-wrapper__input-name'>Phone number</p>
              <Row>
                <Col xs={5}>
                  <SelectWrapper name="phonePrefix" options={codes} />
                </Col>
                <Col xs={7}>
                  <TextfieldWrapper name="phoneNumber" />
                </Col>
              </Row>
            </Col>
            <Col>
              <p className='form-wrapper__input-name'>City</p>
              <SelectWrapper name="city" options={cities} />
            </Col>
            <Col>
              <CheckboxWrapper name="termsOfService" />
            </Col>
            <Button className='form-wrapper__submit' type="submit">Sing up as a driver</Button>
          </Stack>
        </Form>
      </Formik>
    </div>
  );
};

export default FormWrapper;

